import VaultsList from '@/components/vaults/list/VaultsList'
import { VaultsTabFilter } from '@/components/vaults/list/VaultsTabFilter'
import { VaultsTopView } from '@/components/vaults/list/VaultsTopView'
import useVaultList from '@/hooks/vaults/useVaultList'
import { VaultSortRule, VaultSortType } from '@/types/vaults'
import { Tab } from '@cetus/design/src/components/common/SelectTab'
import { SortDropType } from '@cetus/design/src/components/common/SortDropBlock'
import { useAccountBalance, useInterval } from '@cetus/hooks'
import { useAccountStore } from '@cetus/stores'
import { Box, VStack } from '@chakra-ui/react'
import { useEffect, useMemo, useState } from 'react'

function Vaults() {
  const [isYourHoldings, setIsYourHoldings] = useState(false)
  const { fetchAccountBalance } = useAccountBalance()
  const { currentAccount } = useAccountStore()
  const [currTab, setCurrTab] = useState<Tab<object>>({
    label: 'All Vaults',
    value: 'all'
  })
  const {
    totalTvl,
    vaultList,
    fetchVaultList,
    queryOptions,
    setQueryOptions,
    allVaultList,
    getCurrentTabNum,
    selectCoinList,
    onClickSelectCoinList,
    onDeleteSelectCoinList,
    whiteTokenList,
    showSkeletonLoading,
    setShowSkeletonLoading,
    filterVaultList,
    setVaultList,
    sortVaultList
  } = useVaultList()
  const sortByList: SortDropType[] = [
    {
      label: 'APY',
      value: 'apr'
    },
    {
      label: 'TVL',
      value: 'tvl'
    }
  ]
  const [currSortType, setCurrSortType] = useState<SortDropType>({
    label: 'TVL',
    value: 'TVL'
  })

  const tabList = useMemo(() => {
    const list: Tab<object>[] = [
      {
        label: 'All Vaults',
        value: 'all',
        num: getCurrentTabNum('all', isYourHoldings)
      },
      {
        label: 'Cetus',
        value: 'cetus',
        num: getCurrentTabNum('cetus', isYourHoldings)
      },
      {
        label: 'Haedal',
        value: 'haedal',
        num: getCurrentTabNum('haedal', isYourHoldings)
      }
    ]
    return list
  }, [allVaultList, isYourHoldings, selectCoinList])

  const [vaultRefreshInterval, setVaultRefreshInterval] = useState(60000)

  useInterval({
    interval: vaultRefreshInterval,
    callback: () => {
      fetchVaultList(queryOptions, currTab.value, isYourHoldings)
    }
  })

  // 勾选isYourHoldings lptokenInfo初次加载vaultlist是会set 之后走缓存
  // 当前列表筛选
  useEffect(() => {
    if (allVaultList && allVaultList.length > 0) {
      setVaultList(filterVaultList(allVaultList, currTab.value, isYourHoldings))
    }
  }, [isYourHoldings, currTab.value, allVaultList])

  // 初次进入触发
  useEffect(() => {
    fetchVaultList(queryOptions, currTab.value)
  }, [])

  useEffect(() => {
    if (!currentAccount?.address) {
      setIsYourHoldings(false)
    }
  }, [currentAccount?.address])

  useEffect(() => {
    if (selectCoinList && allVaultList && allVaultList.length > 0) {
      setVaultList(filterVaultList(allVaultList, currTab.value, isYourHoldings))
    }
  }, [selectCoinList, currTab.value, isYourHoldings])

  return (
    <VStack gap="0px" w="100%">
      <VaultsTopView isLoading={showSkeletonLoading} total_tvl={totalTvl} />
      <Box h={{ base: '260px', lg: '240px' }} />
      {/* 切换Tab 和 排序 */}
      <VaultsTabFilter
        sortByList={sortByList}
        currTab={currTab}
        tabList={tabList}
        whiteTokenList={whiteTokenList}
        selectCoinList={selectCoinList}
        onClickSelectCoinList={onClickSelectCoinList}
        onDeleteSelectCoinList={onDeleteSelectCoinList}
        showSkeletonLoading={showSkeletonLoading}
        handleChangeTab={(tab: Tab<object>) => {
          if (currTab.value === tab.value) {
            return
          }
          setCurrTab(tab)
          setShowSkeletonLoading(true)
          setTimeout(() => {
            setShowSkeletonLoading(false)
          }, 200)
        }}
        currSortType={currSortType}
        onSortByChange={(type: SortDropType) => {
          queryOptions.sortType = type.value as VaultSortType
          setQueryOptions({ ...queryOptions })
          setCurrSortType(type)
          // 排序不需要fetchList
          sortVaultList({ ...queryOptions }, vaultList)
          setVaultRefreshInterval(60000)
        }}
        sortRule={queryOptions.sortRule}
        handleChangeSortRule={(sortRule: VaultSortRule) => {
          queryOptions.sortRule = sortRule
          setQueryOptions({ ...queryOptions })
          // 排序不需要fetchList
          sortVaultList({ ...queryOptions }, vaultList)
          setVaultRefreshInterval(60000)
        }}
        handleRefresh={() => {
          setShowSkeletonLoading(true)
          fetchAccountBalance()
          setVaultRefreshInterval(60000)
          fetchVaultList(queryOptions, currTab.value, isYourHoldings)
        }}
        isYourHoldings={isYourHoldings}
        onClickIsYourHoldings={() => {
          setIsYourHoldings(!isYourHoldings)
        }}
      />
      <VaultsList isShowPowered={currTab.label !== 'Haedal'} dataList={vaultList} showSkeletonLoading={showSkeletonLoading} />
    </VStack>
  )
}

export default Vaults
