import { vaultsMaps } from '@/constant/vaults'
import useUnstableVaultsListStore from '@/store/unstable-vaults/useVaultsList'
import { default as useVaultsListStore, default as useVaultsStore } from '@/store/vaults/useVaultsList'
import { QueryVaultListOptions, VaultApiInfo } from '@/types/vaults'
import { useGetToken } from '@cetus/hooks/src/useToken'
import { useGetTokenBalance } from '@cetus/hooks/src/useTokenBalance'
import { useAccountStore } from '@cetus/stores'
import { CoinType, Token } from '@cetus/types'
import { isAvailableObject } from '@cetus/utils'
import { d } from '@cetus/utils/src/number'
import { fixCoinType } from '@cetusprotocol/cetus-sui-clmm-sdk'
import { useMemo, useState } from 'react'
import useGetVaultList from './useGetVaultList'

export default function useVaultList() {
  const { getVaultList, getVaultListFromCache } = useGetVaultList()
  const { vaultStatsObj, lpTokenInfo, setLpTokenInfo } = useVaultsStore()
  const { getBalanceInfoFromCache } = useGetTokenBalance()
  const { currentAccount } = useAccountStore()
  const [showSkeletonLoading, setShowSkeletonLoading] = useState<boolean>(true)
  const [whiteTokenList, setWhiteTokenList] = useState<Token[]>([])
  const [selectCoinList, setSelectCoinList] = useState<Token[]>([])
  const { contractVaultObj } = useVaultsListStore()
  const { contractVaultObj: unstableContractVaultObj } = useUnstableVaultsListStore()
  const { fetchTokenInfo } = useGetToken()
  const onClickSelectCoinList = (tokenInfo: Token) => {
    setSelectCoinList(prev => [...prev, tokenInfo])
  }
  const onDeleteSelectCoinList = (tokenInfo: Token) => {
    setSelectCoinList(prev => prev.filter(ele => ele?.coin_type !== tokenInfo?.coin_type))
  }

  const [vaultList, setVaultList] = useState<VaultApiInfo[]>([])
  const [allVaultList, setAllVaultList] = useState<VaultApiInfo[]>([])
  const [queryOptions, setQueryOptions] = useState<QueryVaultListOptions>({
    sortRule: 'desc',
    sortType: 'tvl'
  })

  // 总质押
  const totalTvl = useMemo(() => {
    let total = d(0)
    Object.values(vaultStatsObj).forEach(vaultStatItem => {
      console.log('🚀🚀🚀 ~ useVaultList.ts:28 ~ Object.values ~ vaultStatItem:', vaultStatItem)
      total = total.add(vaultStatItem?.vaultsTvl || '0')
    })

    return total.toString()
  }, [vaultStatsObj])

  // vault列表查询
  const fetchVaultList = async (options: QueryVaultListOptions, currentab = 'all', isYourHoldings: boolean = false) => {
    // 获取时先走缓存
    const cacheList = getVaultListFromCache(options)
    setVaultList(filterVaultList(cacheList, currentab, isYourHoldings))
    // 走接口
    const list = await getVaultList(options)
    if (list?.length > 0) {
      // 如果是初次打开或者新增池子需要重新获取lpToken信息
      if (!isAvailableObject(lpTokenInfo) || Object.values(lpTokenInfo).length !== list.length) {
        const lpTokenInfoObj: Record<string, Token | undefined> = {}
        const coinTypeList: string[] = []
        list.forEach(item => {
          const lpTokenType = item?.lpTokentype || vaultsMaps[item.clmmPoolAddress]?.lpToken?.coin_type
          if (typeof lpTokenType === 'string') {
            coinTypeList.push(lpTokenType)
          }
        })
        const tokenMap = await fetchTokenInfo<CoinType[]>(coinTypeList as CoinType[])
        for (let i = 0; i < list.length; i++) {
          const vault = list[i]
          const lpTokenType = vault?.lpTokentype || vaultsMaps[vault.clmmPoolAddress]?.lpToken?.coin_type
          if (typeof lpTokenType === 'string') {
            lpTokenInfoObj[lpTokenType] =
              vault.category == 'haedal' ? tokenMap?.get(lpTokenType as CoinType) : vaultsMaps[vault.clmmPoolAddress]?.lpToken
          }
        }
        setLpTokenInfo(lpTokenInfoObj)
      }
      const tokenMapList = list.flatMap(item => [item?.displayTokenA, item?.displayTokenB]).filter(Boolean)
      const coinTypeList = tokenMapList.map(item => item?.coin_type).filter(Boolean)
      const tokenMap = await fetchTokenInfo<CoinType[]>(coinTypeList as CoinType[])
      // 列表筛选token 解析vault tokenA tokenB 去重 存储
      const uniqueTokenMap = new Map(
        tokenMapList.map(token => {
          const fixedType = fixCoinType(token.coin_type)
          const tokenInfo = tokenMap?.get(token.coin_type as CoinType)
          return [fixedType, { ...(tokenInfo || {}), coin_type: fixedType }]
        })
      )
      setWhiteTokenList([...uniqueTokenMap.values()])
    }

    setAllVaultList(list)

    console.log('🚀 ~ fetchVaultList ~ list:', list, filterVaultList(list, currentab, isYourHoldings))
    if (list.length > 0) {
      setVaultList(filterVaultList(list, currentab, isYourHoldings))
      const coinTypeList = list
        .map(item => {
          return (contractVaultObj[item?.vaultsId] || unstableContractVaultObj[item?.vaultsId])?.lp_token_type
        })
        .filter(Boolean)
      if (coinTypeList?.length > 0) {
        await fetchTokenInfo<CoinType[]>(coinTypeList as CoinType[])
      }
    }

    setShowSkeletonLoading(false)
  }

  // 通过tokenA tokenB 筛选列表
  const filterVaultListByCoinList = (listOrigin: VaultApiInfo[]) => {
    const firstTokenAddress = selectCoinList[0]?.coin_type ? fixCoinType(selectCoinList[0]?.coin_type) : undefined
    const lastTokenAddress = selectCoinList[1]?.coin_type ? fixCoinType(selectCoinList[1]?.coin_type) : undefined
    const filterArr = listOrigin?.filter((filterItem: any) => {
      if (firstTokenAddress && lastTokenAddress) {
        return (
          (fixCoinType(filterItem.displayTokenA.coin_type) == firstTokenAddress &&
            fixCoinType(filterItem.displayTokenB.coin_type) == lastTokenAddress) ||
          (fixCoinType(filterItem.displayTokenA.coin_type) == lastTokenAddress &&
            fixCoinType(filterItem.displayTokenB.coin_type) == firstTokenAddress)
        )
      } else if (firstTokenAddress) {
        return (
          fixCoinType(filterItem.displayTokenA.coin_type) == firstTokenAddress || fixCoinType(filterItem.displayTokenB.coin_type) == firstTokenAddress
        )
      } else {
        return filterItem
      }
    })
    return filterArr
  }

  // 列表筛选
  const filterVaultList = (listOrigin: VaultApiInfo[], currentab = 'all', isYourHoldings: boolean = false) => {
    console.log('🚀🚀🚀 ~ useVaultList.ts:142 ~ filterVaultList ~ filterVaultList:')

    let filterList: VaultApiInfo[] = []
    let list: VaultApiInfo[] = []
    if (selectCoinList?.length > 0) {
      list = filterVaultListByCoinList(listOrigin)
    } else {
      list = listOrigin
    }
    if (currentab === 'all') {
      filterList = list
    }
    if (currentab == 'cetus') {
      filterList = list.filter(vault => vault.category == 'cetus')
    }
    if (currentab == 'haedal') {
      filterList = list.filter(vault => vault.category == 'haedal')
    }

    if (isYourHoldings && currentAccount?.address) {
      const result = filterMyVaultList(filterList)
      return result
    } else {
      return filterList
    }
  }

  // 筛选我的质押的vault
  const filterMyVaultList = (list: VaultApiInfo[]) => {
    return list.filter(info => {
      const tokenInfo = lpTokenInfo[info.lpTokentype] || vaultsMaps[info.clmmPoolAddress]?.lpToken
      const balance = getBalanceInfoFromCache(tokenInfo)

      if (balance && d(balance.balance).gt(0)) {
        return true
      }
      return false
    })
  }

  // 获取tab数量
  const getCurrentTabNum = (currentab: string, isYourHoldings: boolean = false) => {
    let list
    const result = filterVaultListByCoinList(allVaultList)
    if (currentab == 'all') {
      list = result
    } else if (currentab == 'cetus') {
      list = result.filter(ele => ele.category == 'cetus')
    } else {
      list = result.filter(ele => ele.category == 'haedal')
    }
    if (isYourHoldings && currentAccount?.address && list && list.length) {
      return String(filterMyVaultList(list).length)
    }
    return String((list && list.length) || '')
  }

  const sortVaultList = (options: QueryVaultListOptions, vaultList: VaultApiInfo[]) => {
    const { sortRule, sortType } = options

    const result = vaultList?.sort((a: any, b: any) => {
      if (sortType === 'apr') {
        return sortRule === 'desc'
          ? b.vaultsTotalApy?.replace('%', '') - a.vaultsTotalApy?.replace('%', '')
          : a.vaultsTotalApy?.replace('%', '') - b.vaultsTotalApy?.replace('%', '')
      }

      return sortRule === 'desc' ? b.vaultsTvl - a.vaultsTvl : a.vaultsTvl - b.vaultsTvl
    })
    return result
  }

  return {
    totalTvl,
    filterMyVaultList,
    fetchVaultList,
    vaultList,
    queryOptions,
    setQueryOptions,
    allVaultList,
    getCurrentTabNum,
    selectCoinList,
    onClickSelectCoinList,
    onDeleteSelectCoinList,
    whiteTokenList,
    showSkeletonLoading,
    setShowSkeletonLoading,
    filterVaultList,
    setVaultList,
    sortVaultList
  }
}
