import { CetusTooltip } from '@cetus/design'
import { CoinPairImage, HTextLabelBox, Icon } from '@cetus/ui-kit'
import { formatCurrency } from '@cetus/utils'
import { Box, HStack, Text, VStack } from '@chakra-ui/react'
import PoweredByHaedal from '../../modal/common/PoweredByHaedal'
import { VaultsAction } from '../common/VaultsAction'
import VaultsAprBlock from '../common/VaultsAprBlock'
import { VaultsExpendItem } from '../common/VaultsExpendItem'
import VaultsRewardsBlock from '../common/VaultsRewardsBlock'
import { VaultsListItemType } from '../type'

function VaultsListItemH5<T>({
  openExpend,
  onExpand,
  rewardPerA,
  rewardPerB,
  apiInfo,
  logo_url,
  holdCoinAmounts,
  balanceDisplay,
  sharePoolRate,
  currentAccount,
  totalValue,
  jumpVaultsDetail,
  isShowPowered,
  isShowAprTips
}: VaultsListItemType<T>) {
  return (
    <VStack w="100%" align="flex-start" border="1px solid" borderColor="border" p="8px" borderRadius="12px" gap="16px" bg="bg_fifth">
      <HStack>
        <CoinPairImage
          coinAIconUrl={apiInfo?.displayTokenA?.logo_url}
          coinBIconUrl={apiInfo?.displayTokenB?.logo_url}
          coinACoinType={apiInfo?.displayTokenA?.coin_type}
          coinBCoinType={apiInfo?.displayTokenB?.coin_type}
          w="40px"
          h="40px"
        />
        <VStack>
          <HStack>
            <Text color="text_caption" fontWeight="500">
              {apiInfo?.displayTokenA?.symbol} - {apiInfo?.displayTokenB?.symbol}
            </Text>
            <Box h="20px" lineHeight="18px" bg="bg_secondary" borderRadius="12px" border="1px solid" borderColor="border" p="0 12px">
              <Text fontWeight="500" color="text_highlight">
                {apiInfo?.feeDisplay}
              </Text>
            </Box>
          </HStack>
          {apiInfo?.category == 'haedal' && isShowPowered && <PoweredByHaedal category="Haedal" />}
        </VStack>
      </HStack>
      <VStack w="100%" gap="16px" mt="4px">
        <HTextLabelBox
          label="TVL"
          value={
            <VStack alignItems="flex-end">
              <Text textColor="text_caption" textAlign="right" fontWeight="500">
                {apiInfo.vaultsTvlDisplay}
              </Text>
              {/* {apiInfo.category == 'haedal' && Number(apiInfo.hardCapUSD || 0) > 0 && (
                <AumLimit
                  depositRatio={apiInfo.depositRatio}
                  hardCapUSD={apiInfo.hardCapUSD}
                  vaultTvl={apiInfo.vaultsTvl}
                  value={
                    <Progress
                      h="4px"
                      w="100px"
                      value={apiInfo.depositRatio}
                      bg="primary_opacity.10"
                      sx={{
                        'div[role="progressbar"]': {
                          bg: 'primary'
                        }
                      }}
                    />
                  }
                />
              )} */}
            </VStack>
          }
          labelStyle={{ fontSize: '14px' }}
          valueStyle={{ fontSize: '14px', fontWeight: '500' }}
        />
        <HTextLabelBox
          label={
            <HStack justify="flex-end" gap="2px">
              <Text>APY</Text>
              <CetusTooltip
                placement="auto-start"
                tooltip={
                  <Text lineHeight="20px" fontSize="12px">
                    APY is estimated according to the trading fees and rewards earned over the past 24 hours with daily compounding to be considered.
                  </Text>
                }
              >
                <Icon xlinkHref="#icon-icon_tips" />
              </CetusTooltip>
            </HStack>
          }
          value={
            <VaultsAprBlock
              vaultsLstApyDisplay={apiInfo.vaultsLstApyDisplay}
              vaultsApyDisplay={apiInfo.vaultsApyDisplay}
              vaultsTotalApyDisplay={apiInfo.vaultsTotalApyDisplay}
              vaultsAprDisplay={apiInfo.vaultsAprDisplay}
              category={apiInfo.category}
            />
          }
          labelStyle={{ fontSize: '14px' }}
          valueStyle={{ fontSize: '14px', fontWeight: '500' }}
        />
        <HTextLabelBox
          label="Rewards"
          value={<VaultsRewardsBlock rewardList={apiInfo.vaultsRewards} />}
          labelStyle={{ fontSize: '14px' }}
          valueStyle={{ fontSize: '14px' }}
        />
        <HTextLabelBox
          label="Your Holdings"
          value={currentAccount ? formatCurrency(totalValue, 2) : '--'}
          labelStyle={{ fontSize: '14px' }}
          valueStyle={{ fontSize: '14px', fontWeight: '500' }}
        />
      </VStack>
      <VStack w="100%" gap="8px">
        <VaultsAction
          isOpen={openExpend}
          jumpVaultsDetail={() => {
            jumpVaultsDetail(apiInfo.vaultsId)
          }}
          onExpand={onExpand}
        />
        {openExpend && (
          <VaultsExpendItem
            rewardPerA={rewardPerA}
            rewardPerB={rewardPerB}
            apiInfo={apiInfo}
            logo_url={logo_url}
            holdCoinAmounts={holdCoinAmounts}
            balanceDisplay={balanceDisplay}
            sharePoolRate={sharePoolRate}
          />
        )}
      </VStack>
    </VStack>
  )
}

export default VaultsListItemH5
