import vaultInterface from '@/hooks/vault-interface'
import { useGetRewardPeDay } from '@/hooks/vaults/useVaultsHelper'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { useAccountStore } from '@cetus/stores'
import { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import VaultsListItemH5 from './H5/VaultsListItemH5'
import { VaultsListItemPC } from './PC/VaultsListItemPC'
import { VaultsListItemProps } from './type'

function VaultsListItem(props: VaultsListItemProps) {
  const navigate = useNavigate()
  const { apiInfo, isShowPowered } = props
  console.log('🚀🚀🚀 ~ VaultsListItem.tsx:14 ~ VaultsListItem ~ apiInfo:', apiInfo)
  const { currentAccount } = useAccountStore()
  const useGetContractVaultInfo = vaultInterface.useGetContractVaultInfo(apiInfo?.category == 'haedal')
  const {
    fetchVaultInfoFromContract,
    fetchClmmPoolFromContract,
    lpTokenInfo,
    balanceInfo,
    sharePoolRate,
    holdCoinAmounts,
    totalValue,
    fetchVaultBalance
  } = useGetContractVaultInfo(apiInfo?.vaultsId, apiInfo?.clmmPoolAddress, apiInfo?.tokenA, apiInfo?.tokenB, apiInfo?.isReverse)
  console.log('🚀🚀🚀 ~ VaultsListItem.tsx:26 ~ VaultsListItem ~ balanceInfo:', balanceInfo)

  useEffect(() => {
    fetchVaultInfoFromContract(apiInfo.vaultsId)
  }, [apiInfo.vaultsId])

  useEffect(() => {
    fetchClmmPoolFromContract(apiInfo.poolAddress)
  }, [apiInfo.poolAddress])

  useEffect(() => {
    if (currentAccount?.address && apiInfo.vaultsId && lpTokenInfo?.decimals && apiInfo?.category == 'haedal') {
      fetchVaultBalance(currentAccount?.address, apiInfo.vaultsId, lpTokenInfo?.decimals)
    }
  }, [currentAccount?.address, apiInfo.vaultsId, lpTokenInfo?.decimals, apiInfo?.category])

  const [openExpend, setOpenExpend] = useState<boolean>(false)

  const jumpVaultsDetail = (vaultId: string) => {
    navigate(`/vaults-detail/${vaultId}/${apiInfo?.category}`)
  }

  const { rewardPerA, rewardPerB } = useGetRewardPeDay(balanceInfo?.balanceFormat, apiInfo)

  const onExpand = () => {
    setOpenExpend(!openExpend)
  }

  const { isApp } = useWindowWidth()
  return isApp ? (
    <VaultsListItemH5
      openExpend={openExpend}
      onExpand={onExpand}
      rewardPerA={rewardPerA}
      rewardPerB={rewardPerB}
      apiInfo={apiInfo}
      logo_url={lpTokenInfo?.logo_url}
      holdCoinAmounts={holdCoinAmounts}
      balanceDisplay={balanceInfo?.balanceDisplay}
      sharePoolRate={sharePoolRate}
      currentAccount={currentAccount}
      totalValue={totalValue}
      jumpVaultsDetail={jumpVaultsDetail}
      isShowPowered={isShowPowered}
    />
  ) : (
    <VaultsListItemPC
      openExpend={openExpend}
      onExpand={onExpand}
      rewardPerA={rewardPerA}
      rewardPerB={rewardPerB}
      apiInfo={apiInfo}
      logo_url={lpTokenInfo?.logo_url}
      holdCoinAmounts={holdCoinAmounts}
      balanceDisplay={balanceInfo?.balanceDisplay}
      sharePoolRate={sharePoolRate}
      currentAccount={currentAccount}
      totalValue={totalValue}
      jumpVaultsDetail={jumpVaultsDetail}
      isShowPowered={isShowPowered}
    />
  )
}

export default VaultsListItem
