import { CetusTooltip } from '@cetus/design'
import { useAccountStore } from '@cetus/stores'
import { Icon } from '@cetus/ui-kit'
import NoData from '@cetus/ui-kit/src/components/NoData'
import { HStack, Skeleton, Table, Tbody, Td, Text, Th, Thead, Tr } from '@chakra-ui/react'
import React from 'react'
import { SkeletonViewProps, VaultListProps } from '../type'
import VaultsListItem from '../VaultsListItem'

export function VaultsListPC({ dataList, isAll, showSkeletonLoading, showNoWallet, isShowPowered }: VaultListProps) {
  const { onWalletModal } = useAccountStore()
  return (
    <Table variant="simple_list" w="100%" mt="20px">
      <Thead>
        <Tr>
          <Th>Vaults</Th>
          <Th textAlign="right">TVL</Th>
          <Th textAlign="right">
            <HStack justify="flex-end" gap="2px">
              <Text>APY</Text>
              <CetusTooltip
                tooltip={
                  <Text lineHeight="20px" fontSize="12px">
                    APY is estimated according to the trading fees and rewards earned over the past 24 hours with daily compounding to be considered.
                  </Text>
                }
              >
                <Icon xlinkHref="#icon-icon_tips" />
              </CetusTooltip>
            </HStack>
          </Th>
          <Th textAlign="right">Earnings</Th>
          <Th textAlign="right">Your Holdings</Th>
          <Th textAlign="right">Action</Th>
        </Tr>
      </Thead>

      <Tbody>
        {!showNoWallet && !showSkeletonLoading && dataList.length === 0 && (
          <Tr>
            <Td colSpan={6} w="100%">
              <NoData type="nodata" text="No vaults found" border="none" />
            </Td>
          </Tr>
        )}

        {showNoWallet && (
          <Tr>
            <Td colSpan={6} w="100%" style={{ borderRadius: '16px' }}>
              <NoData type="nowallet" onboard={() => onWalletModal(true)} border="none" />
            </Td>
          </Tr>
        )}

        {showSkeletonLoading && <SkeletonViewPc itemList={[1, 2, 3]} />}
        {!showSkeletonLoading &&
          dataList.map(info => {
            return <VaultsListItem key={info.clmmPoolAddress} apiInfo={info} isShowPowered={isShowPowered} />
          })}
      </Tbody>
    </Table>
  )
}

function SkeletonViewPc(props: SkeletonViewProps) {
  const { itemList } = props
  return itemList.map(item => {
    return (
      <React.Fragment key={item}>
        <Tr
          h="110px"
          sx={{
            td: {
              pb: '16px'
            }
          }}
        >
          <Td>
            <Skeleton w="220px" />
          </Td>
          <Td textAlign="right">
            <HStack justifyContent="end">
              <Skeleton />
            </HStack>
          </Td>

          <Td textAlign="right">
            <HStack justifyContent="end">
              <Skeleton />
            </HStack>
          </Td>

          <Td textAlign="right">
            <HStack justifyContent="end">
              <Skeleton />
            </HStack>
          </Td>

          <Td textAlign="right">
            <HStack justifyContent="end">
              <Skeleton />
            </HStack>
          </Td>

          <Td textAlign="right">
            <HStack justifyContent="end">
              <Skeleton />
            </HStack>
          </Td>
        </Tr>

        <Tr h="16px" />
      </React.Fragment>
    )
  })
}
